r:1px solid rgba(128,128,128,.438);border-radius:10px;color:#000;font-size:.9em;transition:color .3s ease,background-color .3s ease;padding:10px}.home-container .services-block .block-title-sevices .a-more-ser:hover{border-color:blue;color:blue}.home-container .services-block .block-title-sevices .a-more-ser:hover span{transform:translateY(-5px)}.home-container .services-block .block-title-sevices .a-more-ser span{display:flex;justify-content:space-between;align-items:center;transition:transform .3s ease}.home-container .services-block .block-title-sevices .a-more-ser .arrows-icon{font-size:.8em}.home-container .info-with-image{width:100%;display:flex;justify-content:space-around;align-items:center;padding:8em 5em;margin:-4em 0 2em;background-color:rgba(231,231,231,.425);border-radius:70px 70px 0 0;position:relative;z-index:2}.home-container .info-with-image .text-block{width:50%;height:100%;padding-left:20px}.home-container .info-with-image .text-block h1{padding-bottom:5px;font-size:2.5em;font-family:"Gilroy",Arial,sans-serif;transition:opacity 1s ease,transform .8s ease}.home-container .info-with-image .text-block p{padding:10px;text-indent:30px;font-family:"Gilroy",Arial,sans-serif;transition:opacity 1s ease,transform .8s ease}.home-container .info-with-image .text-block section{transition:opacity .8s ease,transform .8s ease}.home-container .info-with-image .text-block ul{margin-left:4em;padding:5px}.home-container .info-with-image .text-block ul li{padding:5px;font-family:"Gilroy",Arial,sans-serif}.home-container .info-with-image .img-block{width:30%;height:100%;display:flex;justify-content:center}.home-container .info-with-image .img-block .men-img{height:700px;border-radius:30px}.home-container .hidden{opacity:0;transform:translateY(100px);transition:opacity .8s ease-out,transform .8s ease-out}.home-container .visible{opacity:1;transform:translateY(0)}