.container-box{width:100%;display:flex;flex-direction:column;align-items:center}.container-box .text-top-cont-box{display:flex;justify-content:center;flex-direction:column;align-items:center;width:90%;margin:1em 0;border-radius:30px;transition:opacity .8s ease-out,transform .8s ease-out}.container-box .text-top-cont-box h2{width:80%;margin-top:1em;transition:opacity .8s ease-out,transform .8s ease-out}.container-box .text-top-cont-box .box-body{width:90%;display:flex;flex-wrap:wrap;justify-content:center;border:1px solid gray;border-radius:30px;margin:1em 0 2em;padding:1em 0;transition:opacity .8s ease-out,transform .8s ease-out;position:relative}.container-box .text-top-cont-box .box{width:25%;margin:1em;padding:1em;border-radius:30px;border:1px solid rgba(128,128,128,.407);transition:opacity .8s ease-out,transform .8s ease-out}.container-box .text-top-cont-box .box h3{margin-bottom:10px;min-height:40px;border-bottom:1px solid rgba(128,128,128,.297);display:flex;align-items:center;transition:opacity .8s ease-out,transform .8s ease-out}.container-box .text-top-cont-box .box h3 .icon{padding:5px 5px;margin:5px;color:blue;background-color:#fff;border-radius:10px}.container-box .text-top-cont-box .box ul{padding-left:10px;list-style:none;padding-left:20px}.container-box .text-top-cont-box .box ul li{margin:10px 10px 0;padding:0 0 10px 15px;border-bottom:1px solid rgba(128,128,128,.297);position:relative;transition:opacity .8s ease-out,transform .8s ease-out}.container-box .text-top-cont-box .box ul li:last-child{border-bottom:none}.container-box .text-top-cont-box .box ul li::before{content:"•";position:absolute;top:-5px;left:-5px;color:blue;font-size:1.5em}.container-box .hidden{opacity:0;transform:translateY(100px)}.container-box .visible{opacity:1;transform:translateY(0)}